import React, { useState } from 'react';
import $ from 'jquery'; 
export default function AddTodo({ addTodo }) {
    const [title, setTitle] = useState("");
    let src = 0;
    const [desc, setDesc] = useState("");
    const submit = (e) => {
        e.preventDefault()
        if (!title || !desc) {
            alert("Title or Description can't be blank")
        }
        else {
            addTodo(title, desc)
            setTitle("")
            setDesc("")
        }
    }

    $('#desc').on('input', function () {
        this.style.height = 'auto';
         
        this.style.height =
                (this.scrollHeight) + 'px';
    });
    return (
        <div className="container my-3">
            <h3>Add Todo</h3>
            <form onSubmit={submit}>
                <div className="mb-3">
                    <label htmlFor="title" className="form-label">Todo's Title</label>
                    <input type="text" value={title} onChange={(e) => setTitle(e.target.value)} className="form-control" id="title" />
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Todo's Description</label>
                    <textarea type="text" value={desc} onChange={(e) => setDesc(e.target.value)} className="form-control" id="desc" />
                </div>
                <button type="submit" className="btn btn-success btn-sm">Add your Todo</button>
            </form>
        </div>
    )

}
